function renderGalleria() {

    var urlParams = new URLSearchParams(window.location.search);
    const tipo = urlParams.get('tipo');

    var url_to_call = apibase.baseurl + '/10101?size=12';
    url_to_call += Paginazione.inizializzaUrl(urlParams);
    const filtro = {
        site_id: 204928,
        "SORT": [{
            "direction": "DESC",
            "property": "data_pubblicazione"
        }]
    };
    $.ajax({
        url: url_to_call,
        method: 'POST',
        data: JSON.stringify(filtro),
        contentType: 'application/json',
        dataType: 'json',
        success: render
    });

    function render(result) {
        if (result != null && result.content.length > 0) {
            let html = [];
            result.content.forEach(function (galleria) {
                let immagine = {};
                if (galleria.components.immagine && galleria.components.immagine.components.length) {
                    immagine = {
                        titolo: galleria.components.immagine.components[0].title,
                        src: baseImgUrl + galleria.components.immagine.components[0].components.file.components[0].properties.thumbaddress
                    }
                    immagine.src = Utils.creaPlaceholder(immagine.src, '//via.placeholder.com/512x320');
                }
                let anteprima = new AnteprimaGalleria();
                anteprima.srcImmagine = immagine.src;
                anteprima.titoloImmagine = immagine.titolo;
                anteprima.linkOggetto = "galleria-immagini.html?id=" + galleria.id;
                anteprima.titoloOggetto = galleria.title;
                html.push('<div class="col-12 col-lg-6">' + VistaGallerie.renderAnteprimaGalleria(anteprima) + '</div>');
                
            });
            $('#fascia_elenco').append('<div class="row">' + html.join('') + '</div>');
            $("#fascia_elenco").append(Paginazione.renderizzaComponente(result));
        } else {
            $("#fascia_elenco").html("Nessun risultato trovato");
        }
    }


}



